import React from 'react';
import ReactDOM from 'react-dom';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';


export default class ReactCSSTransitionGroupExample extends React.Component {
  constructor(props){
      super(props);
      this.state = {
        items: ['', '', '', '']
      };
  }
  _handleAdd = ()=> {
    let newItems = this.state.items.concat([prompt('inputtext!')]);
    this.setState({
      items: newItems
    });
  }
  _handleRemove = (i)=> {
    let newItems = this.state.items.slice();
    newItems.splice(i, 1);
    this.setState({items: newItems});
  }
  render(){
    let items = this.state.items.map(function (item, i){
      return (
        <div key={item} onClick={this._handleRemove.bind(this, i)}>
          {item}
          {/*DON'T<ReactCSSTransitionGroup>*/}
          {/*MountReactCSSTransitionGroupMount*/}
        </div>
      );
    }.bind(this));
    return (
      <div>
        <h3>ReactCSSTransitionGroup/Listanimation</h3>
        <ul>
          <li>add</li>
          <li>Click</li>
          <li>animation</li>
          <li>transitionAppear="true"add</li>
        </ul>
        <button onClick={this._handleAdd}>add</button>
        <p></p>
        {/*ReactCSSTransitionGroupMount*/}
        <ReactCSSTransitionGroup
          transitionName="example"
          transitionEnterTimeout={500}
          transitionLeaveTimeout={300}
          //truex transitionAppear={true}
          transitionAppear >
        {items}
      </ReactCSSTransitionGroup>
      <hr />
      </div>
    );
  }
};
